<template>
    <div class="main">
        <!-- 1ÂäüËÉΩÂå∫ -->
        <div class="tool-content-t">
            <div 
                :class="['mark-btn', isMarkStatus ? 'marked' : '']"
                @click="changeIsMarkStatus">Ê†áËÆ∞‚ù§</div>
            <div class="mark-btn right">ËÆ°Êó∂Ôºö{{ time }} s</div>
        </div>
        <!-- ‰∏≠ÂøÉÊ∏∏ÊàèÂå∫ -->
		<div class="game-content">
            <div 
                v-for="col in cols" :key="Math.random() + col"
                class="game-content-row">
                <span 
                    v-for="row in rows" :key="Math.random() + row"
                    class="game-block"
                    :class="[lattice[(col - 1) * rows + row - 1].isOpen ? 'open' : '', lattice[(col - 1) * rows + row - 1].isMark ? 'mark' : '']"
                    @click.left="handleClickLattice(lattice[(col - 1) * rows + row - 1])"
                    @click.right.prevent="handleSureMinePoint(lattice[(col - 1) * rows + row - 1])">
                    <template v-if="over === 1">
                        <span v-if="lattice[(col - 1) * rows + row - 1].isMine">üí£</span>
                        <span v-else>{{ lattice[(col - 1) * rows + row - 1].mineNum }}</span>
                    </template>
                    <template v-else>
                        <span v-if="lattice[(col - 1) * rows + row - 1].isMark">‚ù§</span>
                        <span v-else>{{ lattice[(col - 1) * rows + row - 1].mineNum }}</span>
                    </template>
                </span>
            </div>
        </div>
        <!-- 2ÂäüËÉΩÂå∫ -->
        <div class="tool-content">
            <div>Ââ©‰ΩôÈõ∑Êï∞Ôºö{{ minePosition.length }}</div>
            <div class="tool">
                <div @click="reStart">ÈáçÂºÄ‰∏ÄÂ±Ä</div>
                <div @click="changeLevel">ÊîπÂèòÈöæÂ∫¶</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
	name: "mineSweeping",
    components: { },
    props: {
        // ÊòØÂê¶Â±ïÁ§∫Ê∏∏ÊàèÁõò
        showGame: {
            required: true,
            type: Boolean
        },
        // Ê∏∏ÊàèÁõòÊ†ºÂ≠êÊï∞ÂíåÈõ∑Êï∞
        gameInfo: {
            required: true,
            type: Array
        }
    },
	data() {
		return {
            // Ê®™ÊéíÊ†ºÂ≠êÊï∞
            rows: 0,
            // Á∫µÊéíÊ†ºÂ≠êÊï∞
            cols: 0,
            // Ê†ºÂ≠êÊÄªÊï∞
            latticeNum: 0,
            // Èõ∑ÁÇπ‰ΩçÁΩÆÔºàÊâæÂá∫‰∏Ä‰∏™Â∞ë‰∏Ä‰∏™Ôºâ
            minePosition: [],
            // Èõ∑ÁÇπ‰ΩçÁΩÆÂ§á‰∏™‰ªΩ
            minePositionBake: [],
            // Ê†ºÂ≠êÂ±ûÊÄß
            lattice: [{
                index: 0, // Ê†ºÂ≠êÁ¥¢Âºï
                mineNum: 0, // Âë®Âõ¥Èõ∑Êï∞
                isMine: false, // ÊòØÂê¶ÊòØÈõ∑
                isOpen: false, // ÊòØÂê¶Â∑≤ÁªèË¢´ÁÇπÂºÄ
                isMark: false, // ÊòØÂê¶Ë¢´Ê†áËÆ∞
            }],
            // Ê∏∏ÊàèÊòØÂê¶ÁªìÊùüÔºö 0-Êú™ÁªìÊùü 1-ÁÇπÂà∞Èõ∑‰∫ÜÂØºËá¥ÁªìÊùü  2-Èõ∑ÁÇπË¢´Ê†áËÆ∞ÂÆå‰∫Ü
            over: 0,
            // ÊòØÂê¶Â§Ñ‰∫éÊ†áËÆ∞Áä∂ÊÄÅÔºàËØ•Áä∂ÊÄÅÁî®‰∫éÁßªÂä®Á´ØÊõø‰ª£PCÁ´ØÂè≥ÈîÆÊ†áËÆ∞Âú∞Èõ∑‰∫ã‰ª∂Ôºâ
            isMarkStatus: false,
            // Ê∏∏ÊàèËÆ°Êó∂
            time: 0,
            // Ê∏∏ÊàèËÆ°Êó∂Âô®
            interval: null
		}
    },
    computed: {
        timeText() {
            return this.getTimeText(this.time);
        }
    },
    watch: {
        over(newVal) {
            switch(newVal) {
                case 1:
                    alert("BOOMÔºåÁà±ÂøÉËΩ∞ÁÇ∏");
                    break;
                case 2:
                    const wrongMark = this.judgeWrongMark();
                    if (wrongMark) {
                        setTimeout(() => {
                            alert(`‰Ω†Êúâ${wrongMark}‰∏™Áà±ÂøÉÊ†áËÆ∞ÈîôÂï¶`);
                        }, 500);
                    } else {
                        this.openAllRest();
                        alert("‰ºòÁßÄÔºåÊâæÂá∫‰∫ÜÂÖ®ÈÉ®ÁöÑ‚ù§");
                    }
                    break;
                default:
                    break;
            }
            if (newVal) {
                window.clearInterval(this.interval);
            }
        },
    },
    created() {
        this.init();
    },
    mounted() {
        this.$nextTick(() => {
            this.setTime();
        });
    },
	methods: {
        // ÂàùÂßãÂåñÊ†ºÂ≠êÁõòÈöêËóèÊï∞ÊçÆ
        init() {
            Object.assign(this.$data, this.$options.data());
            // Áà∂ÁªÑ‰ª∂Â∏¶ËøáÊù•ÁöÑÊ†ºÂ≠êÊï∞ÂíåÈõ∑Êï∞
            this.rows = this.gameInfo[0];
            this.cols = this.gameInfo[1];
            this.latticeNum = this.rows * this.cols;
            // Ëé∑ÂèñÈõ∑ÁÇπ‰ΩçÁΩÆ
            this.getMinePosition();
            // ÁªôÊØè‰∏™Ê†ºÂ≠êËµã‰∫àÊ≠£Á°ÆÁöÑÂ±ûÊÄß
            this.initLattice();
        },
        // Ê∏∏ÊàèÂºÄÂßãËÆ°Êó∂
        setTime() {
            this.interval = setInterval(() => {
                this.time ++;
            }, 1000);
        },
        // Ëé∑ÂèñÊó∂Èó¥
        getTimeText(time) {

        },
        // ÊîπÂèòÊ†áËÆ∞Áä∂ÊÄÅ
        changeIsMarkStatus() {
            this.isMarkStatus = !this.isMarkStatus;
        },
        // ÈöèÊú∫Ëé∑ÂèñÈõ∑ÁÇπ‰ΩçÁΩÆ
        getMinePosition() {
            // ÂÆö‰πâ‰∏Ä‰∏™Êï∞ÁªÑË£Ö‰∏çÈáçÂ§çÁöÑÊ†ºÁÇπ
            let mineArr = [];
            // Âæ™ÁéØÈõ∑Êï∞ÁîüÊàê‰∏çÈáçÂ§çÁöÑÈõ∑ÁÇπ
            for (let n = 0; n < this.gameInfo[2]; n++) {
                const random = Math.floor(Math.random() * this.latticeNum);
                if (mineArr.indexOf(random) === -1) {
                    mineArr.push(random);
                } else {
                    n--;
                }
            }
            this.minePosition = mineArr;
            this.minePositionBake = [].concat(mineArr);
        },
        // Ê†ºÂ≠êÂ±ûÊÄßÂàùÂßãÂåñ
        initLattice() {
            let latticeArr = [];
            for (let n = 0; n < this.latticeNum; n++) {
                let lattice = {
                    index: n, 
                    isOpen: false,
                    mineNum: 0,
                    isMark: false,
                };
                // nÊ†áËÆ∞ÊòØÂê¶ÊòØÈõ∑
                lattice.isMine = this.minePosition.indexOf(n) > -1;
                // Â¶ÇÊûú‰∏çÊòØÈõ∑ÔºåËÆ°ÁÆóÂá∫Ê†ºÂ≠êÂë®Âõ¥8‰∏™ÁÇπÁöÑÈõ∑Êï∞
                if (!lattice.isMine) {
                    this.getMineNumAroundLattice(lattice, n);
                }
                latticeArr.push(lattice);
            }
            this.lattice = latticeArr;
        },
        // ÊâìÂºÄÊâÄÊúâÂâ©‰∏ãÁöÑÊ†ºÂ≠ê
        openAllRest() {
            this.lattice.forEach(item => {
                item.isOpen = true;
            });
        },
        // Ëé∑ÂèñÊ†ºÂ≠êÂë®Âõ¥ÁöÑÈõ∑Êï∞Ôºå
        getMineNumAroundLattice(lattice, index) {
            // ÂÖàËé∑ÂèñÊ†ºÂ≠êÂë®Âõ¥ÁöÑÊúâÊïàÁ¥¢Âºï
            const latticeIndexArr = this.getLatticeIndex(index);
            // Âæ™ÁéØÁ¥¢ÂºïÔºåÁ¥¢ÂºïÂÄºÂú®Èõ∑ÁÇπÊï∞ÁªÑ‰∏≠ÁöÑÔºåÂç≥‰∏∫Èõ∑ÔºåÂΩìÂâçÊ†ºÂ≠êÁöÑÈõ∑ÁÇπÊï∞Âä†1
            latticeIndexArr.forEach(i => {
                if (this.minePosition.indexOf(i) > -1) {
                    lattice.mineNum ++;
                }
            });
        },
        // Ëé∑ÂèñÊ†ºÂ≠êÂë®Âõ¥ÁöÑÊúâÊïàÁ¥¢Âºï
        getLatticeIndex(index) {
            // 0ÂÅöÁ¥¢Âºï‰∏çÂ•ΩÁÆóÔºåÊåâÊ≠£Â∏∏Êï∞Â≠óÊù•ÁÆó
            index++;
            // Â≠òÁ¥¢ÂºïÂÄºÁöÑÂèòÈáè
            let latticeIndexArr = [];
            // ÂΩìÂâçÊ†ºÂ≠ê‰Ωç‰∫éÁ¨¨Âá†Ë°å
            const latticeRow = Math.ceil(index / this.rows);
            // ÂΩìÂâçÊ†ºÂ≠ê‰Ωç‰∫éÁ¨¨Âá†ÂàóÔºàÊ±Ç‰Ωô‰∏∫0ËØ¥ÊòéÊòØÊúÄÂè≥Ëæπ‰∏ÄÂàóÔºâ
            const latticeCol = Math.ceil(index % this.rows) || this.rows;
            // const latticeCol = Math.ceil(index % this.rows) === 0 ? this.rows : Math.ceil(index % this.rows);
            // Á¨¨‰∏ÄË°åÊ≤°Êúâ‰∏ä‰∏ÄË°åÔºå‰∏çÈúÄË¶ÅËÆ°ÁÆóÂáè1ÁöÑË°åÂÄºÔºåÊúÄÂêé‰∏ÄË°åÊ≤°Êúâ‰∏ã‰∏ÄË°åÔºå‰∏çÈúÄË¶ÅËÆ°ÁÆóÂä†1ÁöÑË°åÂÄº
            for (let i = (latticeRow === 1 ? 0 : -1); i < (latticeRow === this.cols ? 1 : 2); i++) {
                // Á¨¨‰∏ÄÂàóÊ≤°ÊúâÂ∑¶ÂàóÔºå‰∏çÈúÄË¶ÅËÆ°ÁÆóÂáè1ÁöÑÂàóÂÄºÔºåÊúÄÂêé‰∏ÄÂàóÊ≤°ÊúâÂè≥ÂàóÔºå‰∏çÈúÄË¶ÅËÆ°ÁÆóÂä†1ÁöÑÂàóÂÄº
                for (let j = (latticeCol === 1 ? 0 : -1); j < (latticeCol === this.rows ? 1 : 2); j++) {
                    // Á¥¢ÂºïÂÄº = (ÂΩìÂâçË°åÂÄº + Ôºà‰∏ä‰∏ÄË°å„Äê-1„Äë/ÂΩìÂâçË°å„Äê0„Äë/‰∏ã‰∏ÄË°å„Äê+1„ÄëÔºâ - 1„Äê1ÊòØÁ¥¢Âºï‰ªé0ÂºÄÂßãÔºåÊâÄ‰ª•ÈúÄË¶ÅÂáèÂéª„Äë) * ÊØèË°åÊ†ºÂ≠êÊï∞ + ÂΩìÂâçÂàóÂÄº + Ôºà‰∏ä‰∏ÄÂàó„Äê-1„Äë/ÂΩìÂâçÂàó„Äê0„Äë/‰∏ã‰∏ÄÂàó„Äê+1„ÄëÔºâ
                    const latticeIndex = (latticeRow + i - 1) * this.rows + (latticeCol + j);
                    // ÂàùÂßãÂÄºÂä†‰∫Ü1ÔºåÊâÄ‰ª•Á¥¢ÂºïË¶ÅÂáèÂéª1ÊâçÂØπ
                    latticeIndexArr.push(latticeIndex - 1);
                }
            }
            return latticeIndexArr;
        },
        // ÁÇπ‰∫ÜÊ†ºÂ≠ê
        handleClickLattice(lattice) {
            // Â¶ÇÊûúÁΩÆ‰∫ÜÊ†áËÆ∞Áä∂ÊÄÅÔºåËØ¥ÊòéÊòØÊâãÊú∫Á´ØÁÇπÈõ∑ÁöÑÊìç‰Ωú
            if (this.isMarkStatus) {
                this.handleSureMinePoint(lattice);
            } else {
                this.leftClick(lattice);
            }
        },
        // Â∑¶ÈîÆÁÇπÂáª
        leftClick(lattice) {
            if (this.over) {
                return false;
            }
            // ÊòØÈõ∑ÔºåÊèêÂâçÁªìÊùüÊàòÊñó
            if (!lattice.isOpen && lattice.isMine) {
                lattice.isOpen = true;
                this.over = 1;
            } else {
                // ÊòØÊï∞Â≠ó
                if (lattice.mineNum) {
                    if (!lattice.isOpen && !lattice.isMark) {
                        lattice.isOpen = true;
                    }
                } else {
                    // ÊòØÁ©∫ÁôΩ
                    const latticeIndexArr = this.getLatticeIndex(lattice.index);
                    this.showWhiteAround(lattice, latticeIndexArr);
                }
            }
        },
        // Âè≥ÈîÆÁ°ÆËÆ§ÊòØÈõ∑ÁÇπ
        handleSureMinePoint(lattice) {
            if (!lattice.isOpen) {
                lattice.isMark = true;
                lattice.isOpen = true;
                this.minePosition.splice(this.minePosition.indexOf(lattice.index), 1);
                this.judgeIsOver();
                // lattice.isMark = !lattice.isMark;
                // if (lattice.isMark) {
                //     lattice.isOpen = true;
                //     this.minePosition.splice(this.minePosition.indexOf(lattice.index), 1);
                //     this.judgeIsOver();
                // } else {
                //     this.minePosition.push(lattice.index);
                // }
            } else {
                if (lattice.isMark) {
                    lattice.isMark = false;
                    lattice.isOpen = false;
                    this.minePosition.push(lattice.index);
                }
            }
        },
        // // ÂΩìÂâçÊ†ºÂ≠êÂë®Âõ¥ÁöÑÈõ∑ÁÇπË¢´Ê≠£Á°ÆÊâæÂá∫Êó∂ÔºåÂèåÂáªÂ°´ÂÖÖÂë®Âõ¥Ê†ºÂ≠ê
        // handleFillAround(lattice) {
        //     // È¶ñÂÖàÂà§Êñ≠ÂΩìÂâçÊ†ºÂ≠êÂ∑≤Ë¢´ÁÇπÂºÄ
        //     if (lattice.isOpen) {
        //         // ÊãøÂà∞Âë®Âõ¥ÊúâÊïàÊ†ºÂ≠êÁ¥¢Âºï
        //         const latticeIndexArr = this.getLatticeIndex(lattice.index);
        //         let mineAllMarked = true;
        //         // Â≠òÈùûÈõ∑ÁöÑÊ†ºÂ≠ê
        //         let tempLatticeIndexArr = [];
        //         // Âæ™ÁéØÊ†ºÂ≠êÔºåÁ°ÆËÆ§Èõ∑Â∑≤ÂÖ®ÈÉ®Ë¢´ÊâæÂá∫
        //         for (let i = 0; i < latticeIndexArr.length; i++) {
        //             if (this.lattice[latticeIndexArr[i]].isMine) {
        //                 if (!this.lattice[latticeIndexArr[i]].isMark) {
        //                     mineAllMarked = false;
        //                     break;
        //                 }
        //             } else {
        //                 tempLatticeIndexArr.push(latticeIndexArr[i]);
        //             }
        //         }
        //         if (mineAllMarked) {
        //             tempLatticeIndexArr.forEach(item => {
        //                 this.lattice[item - 1].isOpen = true;
        //             });
        //         }
        //     } 
        // },
        // Âà§Êñ≠Ê∏∏ÊàèÊòØÂê¶ÁªìÊùü
        judgeIsOver() {
            this.over = this.minePosition.length === 0 ? 2 : 0;
        },
        // Âà§Êñ≠ÈîôËØØÁöÑÊ†áËÆ∞
        judgeWrongMark() {
            let wrongMark = 0;
            this.minePositionBake.forEach(item => {
                if (!this.lattice[item].isMark) {
                    wrongMark ++;
                }
            });
            return wrongMark;
        },
        // Â±ïÁ§∫Âë®Âõ¥ÁöÑÁ©∫ÁôΩÊ†áËÆ∞ÔºåÁõ¥Ëá≥ËæπÁºòÔºàÊ†ºÂ≠êËæπÁºòÊàñËÄÖÊï∞Â≠óÔºâ
        showWhiteAround(lattice, latticeIndexArr) {
            // ÈÅøÂÖçÊúâÈáçÂ§çÁöÑÊï∞ÊçÆÂÅú‰∏ç‰∏ãÊù•ÔºåÂéª‰∏™Èáç
            latticeIndexArr = [...new Set(latticeIndexArr)];
            for (let i = 0; i < latticeIndexArr.length; i++) {
                const item = latticeIndexArr[i];
                // ËÆ°ÁÆó‰∏Ä‰∏™Â∞ë‰∏Ä‰∏™ÔºåÂáèÂ∞ëÂæ™ÁéØ
                latticeIndexArr.splice(i, 1);
                i--;
                if (this.lattice[item].isOpen) {
                    continue;
                }
                this.lattice[item].isOpen = true;
                if (!this.lattice[item].mineNum) {
                    const arr = this.getLatticeIndex(this.lattice[item].index);
                    this.showWhiteAround(this.lattice[item], latticeIndexArr.concat(arr));
                } 
            }
        },
        // ÈáçÂºÄ‰∏ÄÂ±Ä
        reStart() {
            this.init();
            this.setTime();
        },
        // ÊîπÂèòÈöæÂ∫¶
        changeLevel() {
            this.$emit('update:showGame', false);
        }
	}
};
</script>
<style lang="scss" scoped>
@media (max-width: 767px) {
    #app .main {
        height: 100%;
        .tool-content-t {
            width: 100%;
        }
        .tool-content {
            position: static;
            margin: 20px auto;
            width: 50%;
            text-align: center;
            .tool {
                position: static;
                top: 0;
                transform: none;
            }
        }
        .game-content-row {
            width: max-content;
        }
        .game-content {
            width: 100%;
            max-height: 80%;
            overflow: scroll;
        }
    }
} 
.main {
    padding: 10px;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
	border: 2px dashed #0b7777;
    border-radius: 5px;
    .game-content-row {
        margin: 0 auto;
        width: fit-content;
        font-size: 0;
        .game-block {
            display: inline-block;
            margin: 2px;
            background-color: #bbb;
            width: 30px;
            height: 30px;
            line-height: 30px;
            font-size: 14px;
            color: #fff;
            text-align: center;
            cursor: pointer;
            &>span {
                visibility: hidden;
            }
            &.open {
                background-color: #ddd;
                & > span {
                    visibility: visible;
                }
            }
            &.mark {
                color: red;
            }
        }
    }
    .tool-content-t {
        margin: 0 auto;
        width: 300px;
        .mark-btn {
            display: inline-block;
            margin: 10px 0;
            padding: 8px 12px;
            background-color: #0b7777;
            border-radius: 2px;
            width: 100px;
            text-align: center;
            color: #fff;
            cursor: pointer;

            &.marked {
                background-color: #a2891b;
            }
            &.right {
                float: right;
            }
        }
    }
    .tool-content {
        position: absolute;
        top: 20px;
        right: 10px;
        height: 100%;
        .tool {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            &>div {
                margin: 10px 0;
                padding: 8px 12px;
                background-color: #0b7777;
                border-radius: 2px;
                color: #fff;
                cursor: pointer;
            }
        }
    }
}
</style>
